.rules-list
  list-style-type: none
  padding: 0

  &__item
    font-size: 14px
    line-height: 24px
    position: relative
    max-width: 243px
    margin-bottom: 10px

    &:last-child
      margin-bottom: 0

    &::before
      content: ''
      width: 10px
      height: 10px
      background-color: $accent25
      border-radius: 50%
      display: inline-block
      position: absolute
      top: 7px
      left: -20px
