.field
  width: 100%
  height: 44px
  border: 1px solid $accent25
  border-radius: 4px
  padding: 11px 40px 13px 14px
  outline: none
  color: $accent75

  &::placeholder
    color: $accent25

  &:hover, &:focus
    border: 1px solid $accent50

  &_type_dropdown
    cursor: pointer

  &_masked
    cursor: default

    &:hover
      border: 1px solid $accent25

    &:disabled
      background: #fff

.field-wrapper_type_arrow
  position: relative

  &::after
    content: url('../../components/footer/img/arrow_forward.svg')
    display: inline-block
    width: 44px
    height: 44px
    position: absolute
    top: 12px
    right: 0
    text-align: center
    cursor: pointer

.label
  @include label
  font-family: $default-font
  font-size: 12px
  line-height: 15px
  text-transform: uppercase
  font-weight: bold
  color: $accent

  &_type_dropdown
    position: relative

    &::after
      content: ""
      display: inline-block
      position: absolute
      top: 39px
      right: 18px
      width: 9px
      height: 9px
      border: 2px solid $accent50
      border-top: 0
      border-left: 0
      transform: translateY(-50%) rotate(45deg)
      cursor: pointer
